<template>
  <div class="panel-view">
    <CellTips>panel</CellTips>
    <Panel :linkObj="linkObjs" title="Panel 1" more-info="show more" more-link="#"></Panel>

    <CellTips></CellTips>
    <Panel title-only :linkObj="linkObjs" title="Panel 1" more-info="show more" more-link="#"></Panel>
  </div>
</template>

<script>
export default {
  data () {
    return {
      linkObjs: [
        {
          link: '#title-1',
          title: 'Title 1',
          desc: '',
          info: {
            source: 'baidu.com',
            time: '2016-11-11',
            extra: ''
          }
        },
        {
          link: '#title-2',
          title: 'Title 2',
          desc: '',
          img: 'https://imgly.net/img/2gj.jpg'
        }
      ]
    }
  }
}
</script>

<style>
  .panel-view {
    padding: 0 10px;
  }
</style>


