<template>
  <div>
    <cellsTip></cellsTip>
    <cell radio>
      <radio forItem='1r' name="radio">1</radio>
      <radio forItem='2r' name="radio">2</radio>
      <radio forItem='3r' name="radio">3</radio>
      <radio forItem='4r' name="radio" disabled>4</radio>
    </cell>

    <cellsTip></cellsTip>
    <cell checkbox>
      <checkbox forItem='1c' name="checkbox">1</checkbox>
      <checkbox forItem='2c' name="checkbox">2</checkbox>
      <checkbox forItem='3c' name="checkbox">3</checkbox>
      <checkbox forItem='4c' name="checkbox" disabled>4</checkbox>
    </cell>

    <cellsTip></cellsTip>
    <cell form>
      <vwInput label="QQ" input-config='{
        "type": "tel",
        "placeholder": "QQ"
      }'></vwInput>
      <vwInput label="" input-config='{
        "type": "tel",
        "placeholder": ""
      }' addon>
        <addonBtn slot="addon"></addonBtn>
      </vwInput>
      <vwInput label="" input-config='{
        "type": "tel",
        "placeholder": ""
      }' addon>
        <addonImg slot="addon" img="https://imgly.net/img/2gj.jpg"></addonImg>
      </vwInput>
      <vwInput label="" input-config='{
        "type": "date"
      }'></vwInput>
      <vwInput label="" input-config='{
        "type": "datetime-local"
      }'></vwInput>
      <vwInput warn label="" input-config='{
        "type": "text",
        "placeholder": ""
      }'></vwInput>
      <vwInput sucess label="" input-config='{
        "type": "text",
        "placeholder": ""
      }'></vwInput>
    </cell>

    <cellsTip></cellsTip>
    <cell form>
      <vwInput label="" is-switch>
        <vwSwitch slot="switch"></vwSwitch>
      </vwInput>
      <vwInput label="" is-switch>
        <vwSwitch slot="switch" warn></vwSwitch>
      </vwInput>
    </cell>

    <cellsTip></cellsTip>
    <cell form>
      <vwTextarea addon="0/200"></vwTextarea>
    </cell>

    <cellsTip>-</cellsTip>
    <cell form>
      <vwInput select-before input-config='{
        "type": "number",
        "placeholder": ""
      }'>
        <vwSelect slot="selector-before" name='select1' value='+87'>
          <option value="+86">+86</option>
          <option value="+80">+80</option>
          <option value="+84">+84</option>
          <option value="+87">+87</option>
        </vwSelect>
      </vwInput>
    </cell>

    <cellsTip>-</cellsTip>
    <cell form>
      <vwInput select input-config='{
        "type": "number",
        "placeholder": ""
      }'>
        <vwSelect slot='selector' name="select2" value='QQ'>
          <option value=""></option>
          <option value="QQ">QQ</option>
          <option value="Email">Email</option>
        </vwSelect>
      </vwInput>
    </cell>

    <cellsTip>-</cellsTip>
    <cell form>
      <vwInput label='' select-after>
        <vwSelect slot="selector-after" name="select3" value=''>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
        </vwSelect>
      </vwInput>
    </cell>

    <cellsTip></cellsTip>
    <div style="padding:10px;">
      <span>{{rangeValA}}</span>
      <range @change="rangeValForA"></range>
    </div>
    <div style="padding:10px;">
      <span>{{rangeValB}}</span>
      <range step="10" :value="rangeValB" @change="rangeValForB"></range>
    </div>

    <cellsTip></cellsTip>
    <div style="padding:10px;">
      <number></number>
    </div>
    <div style="padding:10px;">
      <number value="10"></number>
    </div>
    <div style="padding:10px;">
      <number value="20" step="5"></number>
    </div>

    <agree text="Read and agree" link="form" title="Agreement" ></agree>
  </div>
</template>

<script>
import page from 'components/article/page.vue'
import pageBody from 'components/article/page-body.vue'
import cellsTip from 'components/cells/cells-tips.vue'
import cell from 'components/cells/cell.vue'
import radio from 'components/forms/radio.vue'
import checkbox from 'components/forms/checkbox.vue'
import vwInput from 'components/forms/input.vue'
import vwTextarea from 'components/forms/textarea.vue'
import addonBtn from 'components/forms/addon-btn.vue'
import addonImg from 'components/forms/addon-img.vue'
import vwSwitch from 'components/forms/switch.vue'
import vwSelect from 'components/picker/select.vue'
import agree from 'components/cells/agree.vue'
import range from 'components/forms/range.vue'
import number from 'components/forms/number.vue'

export default {
  data () {
    return {
      rangeValA: '0',
      rangeValB: '20'
    }
  },
  components: {
    page,
    pageBody,
    cellsTip,
    cell,
    radio,
    checkbox,
    vwInput,
    vwTextarea,
    addonBtn,
    addonImg,
    vwSwitch,
    vwSelect,
    agree,
    range,
    number
  },
  methods: {
    rangeValForA (v) {
      this.rangeValA = v
    },
    rangeValForB (v) {
      this.rangeValB = v
    }
  }
}
</script>


